:root {
  box-sizing: border-box;
  /* CSS HEX */
  --color-primary: #3b5249ff;
  --color-secondary: #dbd8b3ff;
  --black-coffee: #34252fff;
  --tenne-tawny: #cc5803ff;
  --color-disabled: #a167a5ff;
  --color-tertiary: #e8e6ce;
}

html,
body,
#root {
  margin: 0;
  padding: 0;
  height: 100%;
}

h3 {
  margin: 10px 0;
}

.app-header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--color-primary);
  height: 70px;
  padding: 0px 5px;
  left: 0;
  right: 0;
}

/* TODO
header > h1 {
  https://fonts.google.com/specimen/Leckerli+One
  font-family: Leckerli One;
}*/

.app-heading {
  color: white;
}

.app-wrapper {
  max-width: 1000px;
  min-width: 300px;
  margin: 0 auto;
  padding: 30px;

  /* This would make the app wrapper nicely fill the whole sreen
     but doesn't work when there's more than on screen full of content. */
  /* 100% - header height - padding (top+bottom) - app padding */
  /*height: calc(100% - 70px - 60px - 30px);*/
}

.app {
  background-color: var(--color-secondary);
  border-radius: 20px;

  padding: 15px 20px;
  border: 1px solid var(--color-primary);
  height: 100%;
}

.expander-div {
  flex: 100 0 auto;
}

span:hover .tooltip {
  display: block;
}

.tooltip {
  display: none;
  margin-top: 25px;
  font-size: small;
  border: 2px solid var(--color-primary);
  color: var(--color-primary);
  border-radius: 10px;
  padding: 5px;
  background-color: var(--color-secondary);
  position: absolute;
  z-index: 10;
}

/* Button on the app header (login/logout) */
.header-button {
  border: 2px solid var(--color-secondary);
  margin: 0 10px;
}
