.recipe-content {
  display: flex;
  flex-wrap: wrap;
}

/* Ingredients */
.ingredient-sections {
  margin: 10px 20px 10px 0;
  flex: 1 1 300px;
}

.ingredient-section,
.instruction-section,
.notes-section {
  background-color: var(--color-tertiary);
  border-radius: 20px;
  padding: 10px;
  margin: 0 0 7px 0;
}

/* Instructions */
.instruction-sections {
  margin: 10px 20px 20px 0;
  flex: 3 1 300px;
}
