button {
  color: white;
  background-color: var(--color-primary);
  border: none;
  padding: 5px 20px;
  margin: 0 3px;
  border-radius: 5px;
  box-shadow: 4px 4px 2px rgba(0, 0, 0, 0.2);
}

button:hover {
  filter: brightness(125%);
}

button:active {
  background-color: var(--black-coffee);
}

button:disabled {
  background-color: var(--color-disabled);
  filter: brightness(100%);
}
