.filter-input {
  border: none;
  height: 2em;
  border-radius: 7px;
  padding-left: 10px;
  margin-right: 10px;
}

.search-view-controls {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  row-gap: 5px;
  height: 30px;
  min-width: 100%;
}

/* Recipe (category) list */
.recipe-list {
  padding: 20px 0 0 10px;
}

.recipe-category > a,
a:visited,
a:hover,
a:active {
  text-decoration: none;
  color: var(--color-primary);
}

.recipe-list-item {
  padding: 2px 6px;
  text-decoration: none;
}

.recipe-list-item:hover {
  background-color: var(--color-tertiary);
  padding: 1px 6px;
  border: 1px solid black;
  border-radius: 4px;
}

.recipe-list-item:active {
  position: relative;
  left: 3px;
  top: 3px;
  background-color: slategray;
  border: 1px solid black;
}

.recipe-view-header {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 35px;
}
