input[type="checkbox"] {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
  cursor: pointer;
}

.category-checkbox-wrapper {
  display: flex;
  align-items: center;
  height: 30px;
}

.category-checkbox-container {
  display: inline-block;
  position: relative;
  padding-left: 25px;
}

.checkmark {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 20px;
  width: 20px;
  border-radius: 3px;
  background-color: white;
}

.category-checkbox-container:hover input ~ .checkmark {
  background-color: #ccc;
}

.category-checkbox-container input:checked ~ .checkmark {
  background-color: var(--color-primary);
}
/* Style the checkmark/indicator */
.category-checkbox-container .checkmark:after {
  content: "";
  left: 7px;
  top: 2px;
  width: 4px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.category-checkbox-container input:checked ~ .checkmark:after {
  display: block;
}
